<script>
import TableComponent from "@/components/Table/Table.vue";

export default {
  name: "editProtocolOrgComponent",
  components: { TableComponent },
  data() {
    return {
      form: {
        modify_Date: "", // 修改日期
        po_Name: "", // 組織名稱
        MANAGER: "", // 負責人
        location: [], // 廠區
        Location_Id: "",
      },
      loading: false,
      rules: {
        po_Name: [{ required: true, message: "必填", trigger: "blur" }],
        MANAGER: [{ required: true, message: "必填", trigger: "change" }],
        Location_Id: [{ required: true, message: "必填", trigger: "change" }],
      },
      // tableColumns: [
      //   {
      //     id: "index",
      //     label: "項次",
      //     width: "70",
      //   },
      //   {
      //     id: "group_Name",
      //     label: "組別名稱",
      //   },
      //   {
      //     id: "memo",
      //     label: "備註",
      //     width: "225",
      //   },
      // ],
      areaQryEmp: [],
      locations: [],
      selectedFac: [],
    };
  },
  computed: {},
  methods: {
    remoteSelectManagerHandler(evt) {
      this.loading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.loading = false;
        if (!evt.target.value) return;
        this.getAreaQryEmp(evt.target.value);
      }
    },
    visibleHandleChange(e) {
      if (!e) {
        this.areaQryEmp = [];
      }
    },
    managerChangeHandler(val) {
      this.form.manager = val;
    },
    goBack() {
      this.$router.go(-1);
    },
    searchHandler(evt) {
      this.loading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.loading = false;
        if (!evt.target.value) return;
        this.$store.dispatch("getMgrForChoice", evt.target.value);
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const res = await this.$store.dispatch(
              "editProtocalOrg",
              this.form
            );
            if (res.data.isOK) {
              this.$message({
                type: "success",
                duration: 5000,
                showClose: true,
                message: "修改成功",
              });
              this.$router.push({ name: "protocolOrgManagement" });
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async qryFacAttendList() {
      const res = await this.$store.dispatch("qryFacAttendList");
      const data = res.data.data;
      this.locations = data.locations;
      this.selectedFac = data.selectedFac;
    },
    async getAreaQryEmp(word) {
      const res = await this.$store.dispatch("protocalAreaQryEmp", word);
      const data = res.data.data;
      this.areaQryEmp = data;
    },
  },
  async mounted() {
    const item = JSON.parse(this.$route.query.data);
    this.form = { ...this.form, ...item };
    await this.qryFacAttendList();
    this.form.Location_Id = this.selectedFac.find(
      (e) => e.po_Id === item.po_id
    ).location;
    this.form.MANAGER = `${this.form.name}(${this.form.manager})`;
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./editProtocolOrg.html"></template>
